<template>
  <div>
    <!--   -->
    <v-checkbox
      v-model="agreeCheck"
      color="green"
    >
      <template v-slot:label>
        <div @click.stop="">
          <a href="javascript:;" @click="agreeView = true"> </a>
           ,  . ()
        </div>
      </template>
    </v-checkbox>
    <!--    -->
    <v-dialog
      v-model="agreeView"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
      scrollable
    >
      <v-card tile>
        <v-toolbar
          flat
          dark
          color="primary"
        >
          <v-btn
            icon
            dark
            @click="agreeView = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title></v-toolbar-title>
          <v-spacer />
        </v-toolbar>
        <v-card-text>
          <agree-detail />
        </v-card-text>

      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import AgreeDetail from "@/components/AgreeDetail";

export default {
  components: {
    AgreeDetail
  },
  data() {
    return {
      agreeCheck: false,  //  
      agreeView: false,   // 
    }
  },
  watch: {
    agreeCheck() {        //   store 
      this.$store.dispatch('UserAgree', this.agreeCheck);
    }
  },
}
</script>

