<template>
  <!-- 1.   -->
  <v-container>
    <p style="text-align:center">       .</p>
    <v-row align="center" justify="center">
      <div v-for="(food, i) in foods" :key="i" style="text-align:center">
        <div v-if="count==i">
          <img :src="getSrc(food.img)" width="400px" height="400px">
          <h3>{{ food.name }}</h3><br>
        </div>
      </div>
    </v-row>

    <div style="text-align:center">
      <v-btn class="score 1" @click="pref(1)"></v-btn>
      <v-btn class="score 2" @click="pref(2)"></v-btn>
      <v-btn class="score 3" @click="pref(3)"></v-btn>
      <v-btn class="score 4" @click="pref(4)"></v-btn>
      <v-btn class="score 5" @click="pref(5)"></v-btn>
    </div>
    <div style="text-align:center">
      <br>
      <template v-if="count < foods.length - 1">
        <v-btn @click="countdown"></v-btn>
        <v-btn @click="countup"></v-btn>
      </template>
      <template v-else>
        <v-btn @click="submit"></v-btn>
      </template>
    </div>
  </v-container>
</template>

<script>
import Axios from 'axios';
// import Test from '@/components/Test.vue'
export default {
  name:"TestFood",
  components: {
    // Test,
  },
  data() {
    return {
      count : 0,
      tempimg : "@/images/ramyeon.jpg",
      foodpref:{},
      foods : [
        {id: 1, food_id: 6990, name: "", img: "ramyeon"},
        {id: 2, food_id: 7083, name: "", img: "tteokbokki"},
        {id: 3, food_id: 222, name: "", img: "kimchisoup"},
        {id: 4, food_id: 6837, name: "", img: "sweetpotato"},
        {id: 5, food_id: 7009, name: "", img: "jjolnoodle"},
        {id: 6, food_id: 6994, name: "", img: "coldnoodle"},
        {id: 7, food_id: 6848, name: "", img: "fish"},
        {id: 8, food_id: 7374, name: "", img: "chicken"},
        {id: 9, food_id: 276, name: "", img: "donkatsu"},
        {id: 10, food_id: 6906, name: "", img: "chickensoup"},
        {id: 11, food_id: 7340, name: "", img: "eggcustard"},
        {id: 12, food_id: 70, name: "", img: "ricecake"},
        {id: 13, food_id: 7211, name: "", img: "rawmeat"},
        {id: 14, food_id: 7169, name: "", img: "chickenbreast"},
        {id: 15, food_id: 6860, name: "", img: "porkbelly"},
      ],
    }
  },
  mounted(){
    $('.score').click(function() {
      $('.score').removeClass('selected')
      $(this).addClass('selected')
    })
  }, 
  methods: {
    pref(score){
      this.foodpref[this.foods[this.count].food_id] = score;
    },
    submit(){
      Axios.post('/apis/preftest',this.foodpref)
      .then(() => {
        alert(" ")
        this.$router.push('/');
      })
      .catch(() => {
        alert('error')
      })
    },
    getSrc(name) {
      var images = require.context('@/images/', false, /\.jpg$/);
      return images('./' + name + '.jpg')
    },
    countup() {
      if(this.count==14){
        return;
      }
      if(!this.foodpref[this.foods[this.count].food_id]){
        alert(' !')
        return;
      }

      this.count = this.count+1

      $('.score').removeClass('selected')
      if(this.foodpref[this.foods[this.count].food_id]){
        $(`.${this.foodpref[this.foods[this.count].food_id]}`).addClass('selected')
      }
    },
    countdown() {
      if(this.count==0){
        return;
      }
      this.count = this.count-1

      $('.score').removeClass('selected')
      if(this.foodpref[this.foods[this.count].food_id]){
        $(`.${this.foodpref[this.foods[this.count].food_id]}`).addClass('selected')
      }
    }
  },
  // created() {

  // },
  // }
}
</script>
<style>
.selected{
  background-color:grey !important;
}
</style>
