<template>
  <v-container>
    <h3 class="join-title"></h3>
    <v-card class="join-card">
      <!--  -->
      <v-row class="join-row">
        <div class="col-12 col-md-8">
          <v-text-field
            v-model="userId"
            label=" ()"
            :rules="[rules.id.required]"
            @keyup.enter="idCheck"
          />
          <div>
            <p v-if="userIdChk==false" style="color:red;"> .</p>
            <p v-else-if="userIdChk==true" style="color:blue;"> .</p>
          </div>
        </div>
        <div class="col-12 col-md-4">
          <v-btn 
            :disabled="!idIsValid"
            class="join-duplication"
            @click="idCheck"
          >
            
          </v-btn>
        </div>
      </v-row>

      <div class="join-row join-px">
        <!--  -->
        <v-text-field
          v-model="userPw"
          :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
          :rules="[rules.password.required, rules.password.min]"
          :type="show ? 'text' : 'password'"
          label=" ()"
          counter
          @click:append="show = !show"
        />
        <!--   -->
        <v-text-field
          v-model="userPwChk"
          :append-icon="showChk ? 'mdi-eye' : 'mdi-eye-off'"
          :rules="[rules.password.required, rules.password.check]"
          :type="showChk ? 'text' : 'password'"
          label="  ()"
          counter
          @click:append="showChk = !showChk"
        />

        <!--  -->
        <v-text-field
          v-model="userName"
          :rules="[rules.name.required, rules.name.max]"
          label=" ()"
        />

        <!--  -->
        <v-text-field
          v-model="userNick"
          counter="20"
          :rules="[rules.nick.required, rules.nick.max]"
          label=" ()"
        />
      </div>

      <div class="join-row join-subcontent">
        <!--  -->
        <p class="join-description">
          , , , ,      .
        </p>
        <p class="join-description">
          (         .)
        </p>
      </div>

      <div class="join-row join-my">
        <!--  -->
        <v-subheader> ()</v-subheader>
        <span v-if="gender=='f'" class="display-3 pa-2"></span>
        <span v-else class="display-3 pa-2" @click="gender = 'f'"></span>
        <span v-if="gender=='m'" class="display-3 pa-2"></span>
        <span v-else class="display-3 pa-2" @click="gender = 'm'"></span>
      </div>

      <div class="join-row join-px">
        <!--  -->
        <v-text-field
          v-model.number="birthYear"
          label=" ()"
          placeholder="ex) YYYY"
          type="number"
          :rules="[rules.age.min, rules.age.max]"
          prepend-icon="mdi-calendar-text"
          class="join-birthyear"
        />
      </div>

      <v-row class="join-row">
        <!--  -->
        <v-col
          cols="12"
          md="6"
          class="join-px"
        >
          <v-text-field
            v-model.number="heightInfo"
            label=""
            type="number"
            suffix="cm"
          />
        </v-col>
        <!--  -->
        <v-col
          cols="12"
          md="6"
          class="join-px"
        >
          <v-text-field
            v-model.number="weightInfo"
            label=""
            type="number"
            suffix="kg"
          />
        </v-col>
        <!--  -->
        <v-col
          cols="12"
          md="12"
          class="join-px"
        >
          <v-select
            v-model="typeInfo" 
            :items="typeCheck"
            label=" "
          />
        </v-col>
      </v-row>

      <div class="join-row join-px">
        <!--  -->
        <v-text-field
          v-model="address"
          prepend-icon="mdi-map-marker"
          label="()"
        />
      </div>

      <br>

      <div class="join-row join-px join-my">
        <agreement />
      </div>
      
      
      <v-row class="join-row">
        <v-btn 
          text
          :disabled="!formIsValid"
          class="join-btn"
          @click="Join"
        >
          
        </v-btn>
      </v-row>
    </v-card>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";
import Agreement from "@/components/Agreement";
import axios from 'axios';

export default {
  //  .
  name: 'Join',
  components: {
    Agreement,
  },
  data() {
    return {
      userId: "",             //  Id
      userPw: "",             //  pw
      userPwChk: "",          //  pw ()
      userName: "",           //  
      userNick: "",           //  
      birthYear: null,        //   
      gender: null,           //  
      heightInfo: null,       //  
      weightInfo: null,       //  
      typeInfo: '',   //    
      weightType: 'e',        // DB   
      address: null,          //  
      dayKcal: 1860,          //   
      typeCheck: [            //   
        '', 
        '', 
        ''
      ],
      userIdChk: null,        //    
      show: false,            //  
      showChk: false,         //   ()
      rules:{
        id: {                 //   
          required: v => !!v || ' .'
        },
        password: {           //   
          required: value => !!value || ' .',
          min: v => v.length >= 8 || ' 8 .',
          check: v => v == this.userPw || '  .'
        },
        nick: {               //   
          required: v => !!v || ' .',
          max: v => v.length <= 20 || ' 20 .',
        },
        name: {               //   
          required: v => !!v || ' .',
          max: v => v.length <= 20 || ' 20 .',
        },
        age: {                //   
          min: v => 1900 < v || ' .',
          max: v => v < new Date().getFullYear()+1 || ' .',
        },
      }
    }
  },
  computed: {
    userAgree() {           //   
      return this.$store.getters.userAgree
    },
    idIsValid() {           //   
      return this.userId
    },
    formIsValid() {         //   
      return (
        this.userIdChk &&
        this.userPw &&
        this.userPwChk &&
        this.userNick &&
        this.userName &&
        this.gender &&
        this.birthYear &&
        this.userAgree 
      )
    },
    maleKcal() {            //     
      return 88.362 + (13.397 * this.weightInfo) + (4.799 * this.heightInfo) - (5.677 * (new Date().getFullYear() - this.birthYear))
    },
    femaleKcal() {          //     
      return 447.593 + (9.247 * this.weightInfo) + (3.098 * this.heightInfo) - (4.330 * (new Date().getFullYear() - this.birthYear))
    },
    nutriCarb() {           //    
      return Math.round(this.dayKcal * 0.65 / 4)
    },
    nutriProt() {           //    
      return Math.round(this.dayKcal * 0.1 / 4)
    },
    nutriFat() {            //    
      return Math.round(this.dayKcal * 0.25 / 9)
    }
  },
  created() {
    //   home 
    if (this.$cookies.isKey('jwt')) {
      return this.$router.push('/')
    }
  },
  methods: {
    ...mapActions(["Login"]),

    //   
    idCheck() {
      axios.post(`/apis/join/idcheck`, {
        user_id : this.userId
      })
      .then((response) => {
        if (response.data.result == 'duplicated') {
          this.userIdChk = false;
        } else if (response.data.result == 'useable') {
          this.userIdChk = true;
        }
      })
    },
    // 
    Join() {
      //    
      if (this.gender && this.weightInfo && this.heightInfo && this.birthYear) {
        if (this.gender == 'f') {
          this.dayKcal = this.femaleKcal;
        }
        else if (this.gender == 'm') {
          this.dayKcal = this.maleKcal;
        }
      } else {
        this.dayKcal = 1860;
      }
      //     
      if (this.typeInfo == '') {
        this.dayKcal = Math.round(this.dayKcal * 1.2);
        this.weightType = 'd';
      } else if (this.typeInfo == '') {
        this.dayKcal = Math.round(this.dayKcal * 1.55);
        this.weightType = 'e';
      } else if (this.typeInfo == '') {
        this.dayKcal = Math.round(this.dayKcal * 1.725);
        this.weightType = 'u';
      }
      //  
      if (!this.userIdChk) {
        alert("   .");
        return
      } else if (!this.userPw || !this.userPwChk) {
        alert(" .");
        return
      } else if (this.userPw != this.userPwChk) {
        alert("  .");
        return
      } else if (this.userName == "" || this.userName.length > 20) {
        alert("  .");
        return
      } else if (this.userNick == "" || this.userNick.length > 20) {
        alert("  .");
        return
      } else if (this.birthYear != null && (1900 > this.birthYear || this.birthYear > new Date().getFullYear()+1)) {
        alert("  .");
        return
      } else if (this.gender == null) {
        alert(" .");
        return
      } else if (this.heightInfo != null && (100 > this.heightInfo || this.heightInfo > 250)) {
        alert("  .");
        return
      } else if (!this.userAgree) {
        alert(" .");
        return
      } else {
        axios.post(`/apis/join`, {
          user_id : this.userId,
          user_pw : this.userPw,
          user_name : this.userName,   
          nick_name : this.userNick,     
          gender : this.gender,     
          born_year : this.birthYear,  
          address : this.address,    
          height : this.heightInfo, 
          weight : this.weightInfo, 
          weight_type : this.weightType,
          plan_carb : this.nutriCarb,   
          plan_protein : this.nutriProt,   
          plan_fat : this.nutriFat,
          plan_cal : this.dayKcal
        })
        .then((response) => {
          if(response.status === 200){
            //    
            axios.post(`/apis/login`, {
              user_id : this.userId,
              user_pw : this.userPw
            })
            .then((response) => {
              if(response.status === 200 && response.data.token){
                //  
                this.$cookies.set('jwt', response.data.token);      // 
                this.$cookies.set('userId', response.data.userId);  // 

                axios.defaults.headers.common['Authorization'] = response.data.token;

                // store 
                this.Login(response.data.token);                        // 
                this.$store.dispatch('UserId', response.data.userId);   // 

                //      
                this.$router.push('/welcome');
              }
            })
          }else{
            alert(" .");
          }
        })
      }
    } 
  }
}
</script>

<style scoped>
.join-card {
  padding: 20px 16px; 
  margin: 10px auto; 
  min-width: 350px;
  width: 60%;
}
.join-title {
  text-align: center; 
  margin: 20px;
}
.join-row {
  min-width: 320px; 
  max-width: 500px; 
  margin: 0px auto;
}
.join-duplication {
  margin: 12px 0px; 
  width: 150px;
}
.join-subcontent {
  margin: 50px auto 20px; 
  padding: 0px 16px;
}
.join-description {
  font-size: 12px; 
  color: grey; 
  margin: 10px 0px;
}
.join-birthyear {
  padding: 30px 0px 0px;
}
.join-px {
  padding: 0px 10px;
}
.join-my {
  margin: 0px auto 20px;
}
.join-btn {
  width: 100%; 
  color: green; 
  margin: 10px 0px;
}
</style>