<template>
  <v-container>
    <h3 class="login-title"></h3>
    <v-card class="login-card">
      <v-row>
        <v-icon class="login-icon">mdi-lock-outline</v-icon>
      </v-row>
      <!--  -->
      <v-text-field
        v-model="loginData.userId"
        label=""
        required
        outlined
        class="login-input"
      />
      <!--  -->
      <v-text-field
        v-model="loginData.userPw"
        :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
        :type="show ? 'text' : 'password'"
        label=""
        required
        outlined
        class="login-input"
        @click:append="show = !show"
        @keyup.enter="login"
      />
      <v-row>
        <v-btn class="login-btn" @click="login"></v-btn>
      </v-row>
      <v-row>
        <v-btn text class="login-btn" to="/join"></v-btn>
      </v-row>
    </v-card>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";
import axios from 'axios';

export default {
  //  .
  name: "Login",
  data() {
    return {
      loginData: {
        userId: "",   //  ID
        userPw: "",   //  PW
      },
      show: false,    //   
    }
  },
  created() {
    //   home 
    if (this.$cookies.isKey('jwt')) {
      return this.$router.push('/');
    }
  },
  methods: {
    ...mapActions(["Login"]),
    
    // 
    login() {
      axios.post(`/apis/login`, {
        user_id : this.loginData.userId,
        user_pw : this.loginData.userPw
      })
      .then((response) => {
        if(response.status === 200 && response.data.token){
          //  
          this.$cookies.set('jwt', response.data.token);      // 
          this.$cookies.set('userId', response.data.userId);  // 

          axios.defaults.headers.common['Authorization'] = response.data.token;

          // store 
          this.Login(response.data.token);                        // 
          this.$store.dispatch('UserId', response.data.userId);   // 

          //    home 
          this.$router.push('/');
        }else{
          alert("  .");
        }
      })
      
    }
  }
}
</script>

<style scoped>
.login-card {
  padding: 20px 16px; 
  margin: 10px auto; 
  min-width: 360px;
  width: 60%;
  height: 500px;
}
.login-title {
  text-align: center; 
  margin: 20px;
}
.login-icon {
  margin: 20px auto 50px; 
  text-align: center;
  font-size: 100px;
}
.login-input {
  margin: 0px auto; 
  max-width: 320px;
}
.login-btn {
  margin: 0px auto 10px; 
  width: 100%; 
  max-width: 320px;
}
</style>