<template>
  <v-container>
    <h3 class="pwcheck-title"> </h3>
    <v-card class="pwcheck-card">
      <p class="pwcheck-icon"></p>
      <p class="pwcheck-content">
           
      </p> 
      <p class="pwcheck-content">
           .
      </p>
      <v-text-field
        v-model="userPwChk"
        :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
        :type="show ? 'text' : 'password'"
        label=""
        outlined
        class="pwcheck-input"
        @click:append="show = !show"
        @keyup.enter="check"
      />
      <v-row>
        <v-btn 
          class="pwcheck-btn" 
          @click="check"
        >
          
        </v-btn>
      </v-row>
      <p 
        v-if="authentication==false" 
        class="pwcheck-message"
      >
          .
      </p>
    </v-card>
  </v-container>
</template>

<script>
import axios from 'axios';

export default {
  //   .
  name: "MypagePw",
  data() {
    return {
      userPw: null,           //  PW
      userPwChk: null,        //  PW
      show: false,            //  
      authentication: null,   //   
    }
  },
  created() {
    axios.get(`/apis/mypage`)
    .then((response) => {
      this.userPw = response.data.user_pw;
    })
  },
  methods: {
    //   
    check() {
      if (this.userPw == this.userPwChk) {
        this.authentication = true;
        //    
        this.$router.push('/mypage/update');
      }
      else {
        this.authentication = false;
      }
    },
  }
}
</script>

<style scoped>
.pwcheck-card {
  padding: 20px 16px; 
  margin: 10px auto; 
  min-width: 360px;
  width: 60%;
  height: 500px;
}
.pwcheck-title {
  text-align: center; 
  margin: 20px;
}
.pwcheck-icon {
  margin: 10px; 
  text-align: center; 
  font-size: 100px;
}
.pwcheck-content {
  margin: 0px; 
  text-align: center;
}
.pwcheck-input {
  margin: 50px auto 0px; 
  max-width: 320px;
}
.pwcheck-btn {
  margin: 0px auto; 
  width: 100%; 
  max-width: 320px;
}
.pwcheck-message {
  margin: 20px; 
  text-align: center; 
  color: red;
}
</style>