<template>
  <v-container>
    <h3 class="myupdate-title"> </h3>
    <v-card class="myupdate-card">
      <div class="myupdate-row myupdate-px">
        <!-- () -->
        <v-text-field
          label=""
          :placeholder="userId"
          readonly
        />

        <!--  -->
        <v-text-field
          v-model="userPw"
          :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
          :rules="[rules.password.required, rules.password.min]"
          :type="show ? 'text' : 'password'"
          label=" ()"
          counter
          @click:append="show = !show"
        />
        <!--   -->
        <v-text-field
          v-model="userPwChk"
          :append-icon="showChk ? 'mdi-eye' : 'mdi-eye-off'"
          :rules="[rules.password.required, rules.password.check]"
          :type="showChk ? 'text' : 'password'"
          label="  ()"
          counter
          @click:append="showChk = !showChk"
        />

        <!--  -->
        <v-text-field
          v-model="userName"
          :rules="[rules.name.required, rules.name.max]"
          label=" ()"
        />

        <!--  -->
        <v-text-field
          v-model="userNick"
          counter="20"
          :rules="[rules.nick.required, rules.nick.max]"
          label=" ()"
        />
      </div>

      <div class="myupdate-row myupdate-subcontent">
        <!--  -->
        <p class="myupdate-description">
          , , , ,      .
        </p>
        <p class="myupdate-description">
          (         .)
        </p>
      </div>

      <div class="myupdate-row myupdate-my">
        <!--  -->
        <v-subheader> ()</v-subheader>
        <span v-if="gender=='f'" class="display-3 pa-2"></span>
        <span v-else class="display-3 pa-2" @click="gender = 'f'"></span>
        <span v-if="gender=='m'" class="display-3 pa-2"></span>
        <span v-else class="display-3 pa-2" @click="gender = 'm'"></span>
      </div>

      <div class="myupdate-row myupdate-px">
        <!--  -->
        <v-text-field
          v-model.number="birthYear"
          label=" ()"
          placeholder="ex) YYYY"
          type="number"
          :rules="[rules.age.min, rules.age.max]"
          prepend-icon="mdi-calendar-text"
          class="myupdate-birthyear"
        />
      </div>

      <v-row class="myupdate-row">
        <!--  -->
        <v-col
          cols="12"
          md="6"
          class="myupdate-px"
        >
          <v-text-field
            v-model.number="heightInfo"
            label=""
            type="number"
            suffix="cm"
          />
        </v-col>
        <!--  -->
        <v-col
          cols="12"
          md="6"
          class="myupdate-px"
        >
          <v-text-field
            v-model.number="weightInfo"
            label=""
            type="number"
            suffix="kg"
          />
        </v-col>
        <!--  -->
        <v-col
          cols="12"
          md="12"
          class="myupdate-px"
        >
          <v-select
            v-model="typeInfo" 
            :items="typeCheck"
            label=" "
          />
        </v-col>
      </v-row>

      <div class="myupdate-row myupdate-px">
        <!--  -->
        <v-text-field
          v-model="address"
          prepend-icon="mdi-map-marker"
          label="()"
        />
      </div>
      <br>

      <v-row class="myupdate-row">
        <div class="col-12 col-md-10 p-0" style="padding:10px;">
          <v-btn 
            text
            :disabled="!formIsValid()"
            class="myupdate-btn"
            style="color:green;"
            @click="Update"
          >
            
          </v-btn>
        </div>

        <!--  -->
        <div class="col-12 col-md-2" style="padding:10px;">
          <v-dialog v-model="userDelete" persistent max-width="300">
            <template v-slot:activator="{ on, attrs }">
              <v-btn 
                text
                class="myupdate-btn"
                style="color:red;"
                v-bind="attrs"
                v-on="on"
              >
                
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                 ?
              </v-card-title>
              <v-card-text>
                      .
              </v-card-text>
              <v-card-actions>
                <v-spacer />
                <v-btn 
                  text 
                  color="black darken-1" 
                  @click="userDelete = false"
                >
                  
                </v-btn>
                <v-btn 
                  text 
                  color="green darken-1" 
                  @click="Delete"
                >
                  
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>
      </v-row>
    </v-card>
  </v-container>
</template>

<script>
import axios from 'axios';

export default {
  //   .
  name: 'MypageUpdate',
  data() {
    return {
      userId: "",             //  Id
      userPw: "",             //  pw
      userPwChk: "",          //  pw ()
      userName: "",           //  
      userNick: "",           //  
      birthYear: null,        //   
      gender: null,           //  
      heightInfo: null,       //  
      weightInfo: null,       //  
      typeInfo: '',   //    
      weightType: 'e',        // DB   
      address: null,          //  
      dayKcal: 1860,          //   
      typeCheck: [            //   
        '', 
        '', 
        ''
      ],
      show: false,            //  
      showChk: false,         //   ()
      userDelete: false,      //  
      rules:{
        password: {           //   
          required: value => !!value || ' .',
          min: v => v.length >= 8 || ' 8 .',
          check: v => v == this.userPw || '  .'
        },
        nick: {               //   
          required: v => !!v || ' .',
          max: v => v.length <= 20 || ' 20 .',
        },
        name: {               //   
          required: v => !!v || ' .',
          max: v => v.length <= 20 || ' 20 .',
        },
        age: {                //   
          min: v => 1900 < v || ' .',
          max: v => v < new Date().getFullYear()+1 || ' .',
        },
      }
    }
  },
  // computed: {
    
  // },
  created() {
    //   
    axios.get(`/apis/mypage`)
    .then((response) => {
      const userInfo = response.data
      this.userNick = userInfo.nick_name,
      this.userId = userInfo.user_id,
      this.userPw = userInfo.user_pw,
      this.userPwChk = userInfo.user_pw,
      this.userName = userInfo.user_name,
      this.gender = userInfo.gender,
      this.birthYear = userInfo.born_year,
      this.heightInfo = userInfo.height,
      this.weightInfo = userInfo.weight,
      this.weightType = userInfo.weight_type,
      this.address = userInfo.address,
      this.dayKcal = userInfo.plan_cal

      if (this.weightType == 'u') {
        this.typeInfo = '';
      } else if (this.weightType == 'e') {
        this.typeInfo = '';
      } else if (this.weightType == 'd') {
        this.typeInfo = '';
      }
    })
  },
  methods: {
    formIsValid() {         //   
      return (
        this.userPw &&
        this.userPwChk &&
        this.userNick &&
        this.userName &&
        this.gender &&
        this.birthYear
      )
    },
    maleKcal() {            //     
      return 88.362 + (13.397 * this.weightInfo) + (4.799 * this.heightInfo) - (5.677 * (new Date().getFullYear() - this.birthYear))
    },
    femaleKcal() {          //     
      return 447.593 + (9.247 * this.weightInfo) + (3.098 * this.heightInfo) - (4.330 * (new Date().getFullYear() - this.birthYear))
    },
    nutriCarb() {           //    
      return Math.round(this.dayKcal * 0.65 / 4)
    },
    nutriProt() {           //    
      return Math.round(this.dayKcal * 0.10 / 4)
    },
    nutriFat() {            //    
      
      return Math.round(this.dayKcal * 0.25 / 9)
    },
    //  
    Update() {
      
      //   null 
      if (this.heightInfo == '') {
        this.heightInfo = null;
      }
      if (this.weightInfo == '') {
        this.weightInfo = null;
      }
      if (this.address == '') {
        this.address = null;
      }
      //    
      if (this.gender && this.weightInfo && this.heightInfo && this.birthYear) {
        if (this.gender == 'f') {
          this.dayKcal = this.femaleKcal();
        }
        else if (this.gender == 'm') {
          this.dayKcal = this.maleKcal();
        }
      } else {
        this.dayKcal = 1860;
      }
      //     
      if (this.typeInfo == '') {
        this.dayKcal = Math.round(this.dayKcal * 1.2);
        this.weightType = 'd';
      } else if (this.typeInfo == '') {
        this.dayKcal = Math.round(this.dayKcal * 1.55);
        this.weightType = 'e';
      } else if (this.typeInfo == '') {
        this.dayKcal = Math.round(this.dayKcal * 1.725);
        this.weightType = 'u';
      }
      //  
      if (!this.userPw || !this.userPwChk) {
        alert(" .");
        return
      } else if (this.userPw != this.userPwChk) {
        alert("  .");
        return
      } else if (this.userName == "" || this.userName.length > 20) {
        alert("  .");
        return
      } else if (this.userNick == "" || this.userNick.length > 20) {
        alert("  .");
        return
      } else if (this.birthYear != null && (1900 > this.birthYear || this.birthYear > new Date().getFullYear()+1)) {
        alert("  .");
        return
      } else if (this.gender == null) {
        alert(" .");
        return
      } else if (this.heightInfo != null && (100 > this.heightInfo || this.heightInfo > 250)) {
        alert("  .");
        return
      } else {
        axios.put(`/apis/mypage`, {
          user_id : this.userId,
          user_pw : this.userPw,
          user_name : this.userName,   
          nick_name : this.userNick,     
          gender : this.gender,     
          born_year : this.birthYear,  
          address : this.address,    
          height : this.heightInfo, 
          weight : this.weightInfo, 
          weight_type : this.weightType,
          plan_carb : this.nutriCarb(),   
          plan_protein : this.nutriProt(),   
          plan_fat : this.nutriFat(),
          plan_cal : this.dayKcal
        })
        .then((response) => {
          if (response.status === 200){
            //    mypage 
            this.$router.push('/mypage');
          } else {
            alert("  .");
          }
        })
      }
    },
    Delete() {
      axios.delete(`/apis/mypage`)
      .then(() => {
        //  
        this.$cookies.remove('jwt');
        this.$cookies.remove('userId');

        axios.defaults.headers.common['Authorization'] = undefined;

        // store   
        this.$store.dispatch('Logout', null);
        this.$store.dispatch('UserId', null);

        //    home 
        this.$router.push('/');
      })
    } 
  }
}
</script>

<style scoped>
.myupdate-card {
  padding: 50px 16px; 
  margin: 10px auto; 
  min-width: 360px;
  width: 60%;
}
.myupdate-title {
  text-align: center; 
  margin: 20px;
}
.myupdate-row {
  min-width: 320px; 
  max-width: 500px; 
  margin: 0px auto;
}
.myupdate-duplication {
  margin: 12px 0px; 
  width: 150px;
}
.myupdate-subcontent {
  margin: 50px auto 20px; 
  padding: 0px 16px;
}
.myupdate-description {
  font-size: 12px; 
  color: grey; 
  margin: 10px 0px;
}
.myupdate-birthyear {
  padding: 30px 0px 0px;
}
.myupdate-px {
  padding: 0px 10px;
}
.myupdate-my {
  margin: 0px auto 20px;
}
.myupdate-btn {
  width: 100%; 
}
</style>